<template>

<v-layout row justify-center>
  <v-container>
    <v-dialog
      persistent
      max-width="900"
      v-model="isVisible"
    >
      <v-card
      >
        <v-card-title primary-title class="border-bottom">
          Upload avatar
        </v-card-title>

        <div class="content d-flex">

            <v-col cols="12" md="5">
              <v-file-input 
              refs="input"
              v-model="file"
              label="File input"
              accept="image/*"
              @change="setImage"
              :aspect-ratio="16 / 9"
              >
                
              </v-file-input>
            </v-col>

             <div class="content">
                <section class="cropper-area">
                 <div class="img-cropper">
                  <vue-cropper
                    ref="cropper"
                    :src="imgSrc"
                    alt="Source Image"
                    preview=".preview"
                  
                  >
                  </vue-cropper>
                </div>
              </section>
            </div>
   
        </div>

        <v-card-actions>
          <v-spacer></v-spacer>

          <btn-custom 
            :outlined="true"
            title="Save"
            v-on:action="save()"
            color="blue darken-1"
            type="save"
          >
          </btn-custom>
          
          <btn-custom 
            :outlined="true"
            title="Close"
            v-on:action="close()"
            color="blue darken-1"
            type="close"
          >
          </btn-custom>
        </v-card-actions>

      </v-card>
  </v-dialog>
  </v-container>
</v-layout>

</template>
<style scoped="">

.cropper-area {
  width: 300px;
  height: 350px;
  margin: 0 auto;
}


</style>

<script>
import DropZone from "@/components/custom/DropZone.vue";
import Modal from "@/store/models/modal";

export default {

  components: {
    'dropzone': DropZone
  },
    
  props: {
    isVisible: {
      type: Boolean,
      default: false,
    }
  },


  data() {
    return {

      imgSrc: "https://quanlymuabanxe.s3-ap-southeast-1.amazonaws.com/avatars/default.png",
      file: null,
      cropImg: null,
      data: {
        left: 92,
        top: 36.9375,
        width: 200,
        height: 300,
      }
    };
  },

  mounted(){
    this.setCropBoxData();
  },


  methods: {
    close(){
     this.$emit('update:isVisible', false) 
    },

    save(){

      console.log(this.$refs.cropper.getData())
      this.cropImg = this.$refs.cropper.getCroppedCanvas().toDataURL();

    },

    setCropBoxData() {

      this.$refs.cropper.setCropBoxData(this.data);
    },

    setImage(e) {

      const file = this.file
      if (file.type.indexOf('image/') === -1) {
        alert('Please select an image file');
        return;
      }
      if (typeof FileReader === 'function') {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.imgSrc = event.target.result;

          this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert('Sorry, FileReader API not supported');
      }
    },
  },


};
</script>
